
export default function sketch(p){
      let canvas;

      let cols, rows;
      let scl = p.windowWidth/20;
      let w;
      let h;

      let flying = 0;

      let terrain = [];


      p.setup = () => {
            canvas = p.createCanvas(p.windowWidth, p.windowHeight, p.WEBGL);

            cols = p.windowWidth/2;
            rows = 5;

            sclSetter();

            terrainSetup();

            // cols = w / 2;
            // rows = h/ scl;
            p.background(0, 1);
            p.fill(0)

      }
  
      p.draw = () => {




            p.stroke('#B58900'); p.strokeWeight(3); p.fill('#073642'); p.background('#073642');
      
            terrainGenerator();

            p.background('#073642');
            p.rotateX(p.PI/2);
            p.translate(-p.windowWidth/2, -p.windowHeight/2)
            p.translate(-p.windowWidth/2, -p.windowHeight/2);

            for (var y = 0; y < rows-1; y++) {
                  p.beginShape(p.TRIANGLE_STRIP);
                  for (var x = 0; x < cols; x++) {
                        p.vertex(x*scl, y*scl, terrain[x][y]);
                        p.vertex(x*scl, (y+1)*scl, terrain[x][y+1]);
                  }
                  
                  p.endShape();
            }
      }
  

      p.windowResized = () => {
            p.resizeCanvas(p.windowWidth, p.windowHeight);
            cols = p.windowWidth;

            terrainSetup();

            terrainGenerator();



      }

      function terrainGenerator() {

            flying -= 0.002;
            var yoff = flying;

            for (var y = 0; y < rows; y++) {
                  var xoff = 0;
                  for (var x = 0; x < cols; x++) {
                        terrain[x][y] = p.map(p.noise(xoff, yoff), 0, 2, -200, 200);
                        xoff += 0.1;
                  }
                  yoff += 0.1;
            }
      }

      function terrainSetup() {

            for (var x = 0; x < cols; x++) {
                  terrain[x] = [];
                  for (var y = 0; y < rows; y++) {
                        terrain[x][y] = 0; //specify a default value for now
                  }
            }

      }

      function sclSetter() {
            scl = 20
      }

  }
